.py-60 {
  padding: 60px 0;
}

.about__main {
  display: flex;
  justify-content: space-between;
}

.about__title h5 {
  font-size: 1.8rem;
  font-weight: 100;
  text-transform: capitalize;
}

.about__item {
  width: 100%;
}

.about__content {
  width: 100%;
}

.about__img__stand {
  width: 60%;
  margin-left: 159px;
  overflow: hidden;
}

.about__item img.hinhdung {
  object-fit: cover;
  border-radius: 20%;
  filter: brightness(100%);
  width: 100%;
  transition: 0.7s all ease-in-out;
}

.about__img__stand:hover img {
  transform: scale(1.15);
}

.about__item {
  position: relative;
}

.about__img__col {
  width: 60%;
  height: 20%;
  overflow: hidden;
}

.about__img__col img.ngang {
  position: absolute;
  bottom: 0px;
  right: 0;
  border-radius: 10px;
  width: 50%;
  object-fit: cover;
  filter: brightness(80%);
  transition: 0.7s all ease-in-out;
  width: 30%;
  height: 20%;
  object-fit: cover;
}

.about__img__col:hover img.ngang {
  transform: scale(1.25);
}

.about__content {
  width: 100%;
  margin: 0 30px;
}

h2.content__title {
  font-size: 2rem;
  letter-spacing: 8px;
  text-transform: capitalize;
  font-weight: 100;
}

h2::after {
  content: "";
  background-color: black;
  width: 100%;
  height: 0.2px;
  display: block;
  margin-top: 20px;
}

.Content__title_person ul {
  display: flex;
  justify-content: space-between;
  padding: 0;
  padding-bottom: 50px;
}

.Content__title_person ul li {
  list-style: none;
}
.Content__title_person ul li a {
  text-decoration: none;
  font-size: 1rem;
  text-transform: capitalize;
  font-weight: 200;
  color: black;
}

.Content__title_person ul li:hover a {
  color: rgb(135, 7, 7);
}

.view__more {
  padding-top: 50px;
}

.btn__about {
  background-color: #2ecc71;
  color: white;
  width: 130px;
  height: 50px;
  border: 1px solid white;
  border-radius: 5px;
  text-transform: capitalize;
  transition: all 0.8s;
}

button:hover {
  background-color: rgb(135, 7, 7);
  cursor: pointer;
}