.py-50 {
  padding: 50px 0;
}

.slick-next {
  right: 50px;
  top: 260px !important;
  z-index: 100;
}

.slick-prev {
  top: 260px !important;
  left: 50px;
  z-index: 100;
}

.new__image {
  width: 100%;
  overflow: hidden;
}

.new__image img {
  width: 100%;
  transition: all 0.5s ease-in-out;
}

.new__image img:hover {
  transform: scale(1.15);
  cursor: pointer;
}