.py-50 {
  padding: 50px 0;
}

.clipgallery__title h4 {
  text-transform: capitalize;
  font-size: 1.5rem;
  font-weight: 100;
}

.clipvideo__content {
  background-image: url("../image/gallary/album14.jpg");
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  filter: brightness(60%);
  width: 100%;
  height: 100vh;
}
.clipvideo__content .clipvideo__item {
  display: flex;
  align-items: center;
  justify-content: center;
}