.album__gallary{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 10px;
    width: 100%;

}
.album__item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  
}

.album__item.icon1 {
    grid-column: 1 / 3;
    grid-row: 1 / 3;

}
.album__item.icon10 {
    grid-column: 3/ 5;
    grid-row: 3 / 5;

}
