.py-30 {
  padding: 30px 0;
}

.services__title {
  text-align: center;
}
.services__title p.title {
  font-size: 1.5rem;
  color: rgb(162, 160, 160);
  font-weight: 300;
}
.services__title h1 {
  font-size: 3rem;
  font-weight: 500;
  padding: 20px;
  margin: 0;
}
.services__title p.service__title__content {
  font-size: 1rem;
  padding-bottom: 30px;
}

.service__content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  gap: 10px;
}
.service__content .service__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: grayscale(20%) contrast(80%);
}

.service__item {
  position: relative;
}
.service__item .service__detail {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(235, 151, 186, 0.6);
  opacity: 0;
  transition: all 0.5s;
}

.service__item:hover .service__detail {
  opacity: 1;
  transition: 0.8s;
}

.service__icon {
  text-align: center;
  position: absolute;
  top: 30%;
  left: 150px;
}

.service__icon h5 {
  color: white;
  text-transform: uppercase;
}